:root {

	/* Z-index levels */
}

.root {
	border: 1px solid #424242;
	background: #fff;
	max-height: 400px;
	overflow-y: auto;
	position: relative;
}

.focused {}

.option {
	cursor: pointer;
}

.focused .activeOption,
.root:focus .activeOption {
	-webkit-box-shadow: inset 0 0 1px 0 #fff;
	        box-shadow: inset 0 0 1px 0 #fff;
	outline: 4px auto #34495e;
}

.disabledOption {
	background-color: #bbb;
	cursor: default;
}

.selectedOption {
	background-color: #34495e;
	color: #fff;
}
